import React from "react";
import { FaCheck, FaTrash } from "react-icons/fa";
import FlipMove from "react-flip-move";

class ListItem extends React.Component {
  constructor(props) {
    super(props);
    this.handleBtnClick = this.handleBtnClick.bind(this);
  }
  handleBtnClick(e) {
    const selectedItem = e.target.parentNode.parentNode;
    if (e.target.className === "btn check") {
      selectedItem.classList.toggle("checked");
    }
    if (e.target.className === "btn trash") {
      selectedItem.classList.add("fall");
      selectedItem.addEventListener("transitionend", () => {
        selectedItem.remove();
      });
    }
  }
  render() {
    const items = this.props.items;
    const itemsList = items.map((item) => {
      if (item.text !== "") {
        return (
          <div className="todo-list" key={item.key}>
            <li>{item.text}</li>
            <div className="btns">
              <button className="btn check" onClick={this.handleBtnClick}>
                <FaCheck className="pointernone" />
              </button>
              <button className="btn trash" onClick={this.handleBtnClick}>
                <FaTrash className="pointernone" />
              </button>
            </div>
          </div>
        );
      }
      return null;
    });
    return (
      <div>
        <FlipMove duration={300} easing="ease-in-out">
          {itemsList}
        </FlipMove>
      </div>
    );
  }
}

export default ListItem;
